import Vue from 'vue'
import VueI18n from 'vue-i18n'
import App from './App.vue'
import router from './router'
import { Select, Anchor } from 'ant-design-vue';

Vue.config.productionTip = false
Vue.use(VueI18n)
Vue.use(Select);
Vue.use(Anchor);

const i18n = new VueI18n({
  locale: 'English',
  messages: {
    'English': require('./locale/en')
  }
})


new Vue({
  i18n,
  router,
  render: h => h(App)
}).$mount('#app')
